import Jumbo from "../components/Jumbo"
import DoomCat from '../components/DoomCat'
import { useData } from '../dataContext'

const DoomCats = () => {
  const {
    catBalance,
    catIdsOfOwnerByIndexes
  } = useData()

  return (
    <Jumbo>
      <div className="mb-8 text-center text-xl">You own {catBalance.toString()} DoomCats</div>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-20">
        {catIdsOfOwnerByIndexes.map((catId, index) => {
          return (
            <DoomCat tokenId={catId} key={index} />
          )
        })}
      </div>
    </Jumbo>
  )
}

export default DoomCats
